<template>
  <v-card class="portfolio-card">
    <v-img
      class="white--text align-end"
      :aspect-ratio="16/9"
      :src="portfolio.image"
      :lazy-src="portfolio.lazy || false"
    >
      <v-card-title>
        {{portfolio.title}}
      </v-card-title>
    </v-img>

    <v-card-subtitle class="pb-0">
      <common-skill-button
        v-for="(icon, idx) in portfolio.icons"
        :key="`icon-${idx}`"
        :rightMargin="(portfolio.icons.length - 1 - idx) * 44"
        :icon="icon"
        top
      />

      <span>{{portfolio.role}}</span>
    </v-card-subtitle>

    <v-card-actions>
      <v-btn
        :color="techList[currentTech].material"
        text
        @click="$viewer.show()"
      >
        Gallery
      </v-btn>
      <v-btn
        :color="techList[currentTech].material"
        :disabled="!portfolio.href"
        :href="portfolio.href"
        target="_blank"
        text
      >
        Live Demo
      </v-btn>
    </v-card-actions>

    <viewer
      :options="options"
      :style="{ display: 'none' }"
      @inited="inited"
    >
      <template v-for="(image,index) in portfolio.images">
        <img
          :src="image.source" class="image" :key="index"
          :data-src="image.source"
          :alt="image.title"
        >
      </template>
    </viewer>
  </v-card>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { techList } from '@/constants'

const options = {
  rotatable: false,
  scalable: false,
  transition: false,
  url: 'data-src'
}

export default {
  props: ['portfolio'],
  data: () => ({
    techList,
    options
  }),
  computed: {
    ...mapState(['currentTech'])
  },
  methods: {
    ...mapActions(['nextTech']),
    inited (viewer) {
      this.$viewer = viewer
    }
  }
}
</script>

<style lang="scss">
.portfolio-card {
  width: 100%;
  max-width: 500px;
  .v-card {
    &__title {
      background-color: #000a;
    }

    &__subtitle {
      position: relative;
    }
  }
}
</style>
