<template>
  <v-tooltip
    v-if="iconList[icon]"
    bottom
  >
    <template v-slot:activator="{ on }">
      <v-btn
        fab
        absolute
        bottom
        right
        small
        :style="{'margin-right': `${rightMargin}px`}"
        v-on="on"
        v-bind="$attrs"
      >
        <v-img
          :src="iconList[icon].icon"
          max-height="26"
          max-width="26"
        />
      </v-btn>
    </template>
    <span>{{iconList[icon].label}}</span>
  </v-tooltip>
</template>

<script>
import { iconList } from '@/constants'

export default {
  data: () => ({
    iconList
  }),
  props: {
    icon: {
      type: String,
      default: ''
    },
    rightMargin: {
      type: Number,
      default: 0
    }
  }
}
</script>
