<template>
  <div
    class="wizard"
    v-bind="$attrs"
  >
    <div class="wizard__bright-side">
      <div class="wizard__photo" />
      <div
        class="wizard__color-overlay smooth"
        :style="{
          backgroundColor: techList[currentTech].light
        }"
      >
        <img src="/img/me.png" width="100%" />
      </div>
      <div class="wizard__white-overlay">
        <img src="/img/me.png" width="100%" />
      </div>
      <img class="wizard__brighter" src="/img/me.png" width="100%" />
      <div class="wizard__mask smooth-fast" :class="{ light: !!prevTech }" />
    </div>
    <home-tech-ball class="wizard__tech-ball" />
    <div class="wizard__dark-side" />
  </div>
</template>

<script>
import { techList } from '@/constants'
import { mapState } from 'vuex'

export default {
  data: () => ({
    techList
  }),
  computed: {
    ...mapState(['currentTech', 'prevTech'])
  }
}
</script>

<style lang="scss">
.wizard {
  padding: 50%;
  position: relative;

  &__photo {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;

    &:after {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-image: url(/img/me.png);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      opacity: .2;
    }
  }

  &__color-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: radial-gradient(circle at 15% 45%, transparent 55%, black 150%);

    > img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      mix-blend-mode: color-burn;
    }

    opacity: .4;
  }

  &__white-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: radial-gradient(circle at 15% 45%, white 33.3%, black 100%);

    > img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      mix-blend-mode: color-burn;
    }

    opacity: .3;
  }

  &__brighter {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    mask-image: radial-gradient(circle at 15% 45%, white, transparent);
    mask-size: 100%;
  }

  &__mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;

    opacity: 0;

    &.light {
      opacity: .5;
    }
  }

  &__bright-side {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    mask-image: url(/img/me.png);
    mask-size: 100%;
  }

  &__dark-side {
    position: absolute;
    pointer-events: none;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;

    &:after {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-image: url(/img/me_dark.png);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      opacity: .5;
    }

    mask-image: url(/img/me_dark.png);
    mask-size: 100%;
  }

  &__tech-ball {
    position: absolute;
    top: 45%;
    left: 15%;
    padding: 12%;
    transform: translate(-50%, -50%);
  }
}
</style>
