<template>
  <v-card class="flex-grow-1">
    <v-progress-linear
      class="smooth"
      :color="techList[currentTech].material"
      :value="skill.value"
      height="10"
      striped
    />
    <v-card-title :style="{position: 'relative'}">
      <h3 class="d-flex align-center">{{ skill.title }}</h3>
      <common-skill-button
        v-for="(icon, iconIdx) in skill.icons"
        :key="1 + iconIdx"
        :rightMargin="(skill.icons.length - 1 - iconIdx) * 44"
        :icon="icon"
      />
    </v-card-title>
    <v-divider />
    <v-card-text>
      <v-list
        dense
        subheader
      >
        <v-subheader v-if="skill.years">
          {{ skill.years }} years of experience
        </v-subheader>
        <v-list-item
          v-for="(component, componentIdx) in skill.components"
          :key="-componentIdx"
        >
          <v-list-item-icon>
            <v-icon
              class="smooth"
              :color="techList[currentTech].material"
              size="30"
            >mdi-check</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <h3>{{ component }}</h3>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { techList } from '@/constants'

export default {
  props: ['skill'],
  data: () => ({
    techList
  }),
  computed: {
    ...mapState(['currentTech'])
  },
  methods: {
    ...mapActions(['nextTech'])
  }
}
</script>
