<template>
  <div
    class="tech-ball tapable"
    @click="nextTech"
    v-bind="$attrs"
  >
    <div
      :style="`
        filter: brightness(200%) blur(20px) drop-shadow(0 0 30px ${prevTech ? techList[prevTech].lighter : techList[currentTech].lighter}) drop-shadow(0 0 50px ${prevTech ? techList[prevTech].light : techList[currentTech].light};
        background-image: url(${prevTech ? techList[prevTech].logo : techList[currentTech].logo})
      `"
      :class="{ fadeIn: !prevTech, fadeOut: !!prevTech }"
      class="tech-ball__logo-back-light"
    />
    <div class="tech-ball__ray-box">
      <div
        v-for="(ray, idx) in backRays"
        :key="-idx"
        class="tech-ball__ray-positioner"
        :style="ray.style"
      >
        <div
          class="tech-ball__ray"
          :style="ray.rayStyle"
        />
      </div>
    </div>
    <div
      :style="`background-image: url(${prevTech ? techList[prevTech].logo : techList[currentTech].logo})`"
      :class="{ fadeIn: !prevTech, fadeOut: !!prevTech }"
      class="tech-ball__logo"
    />
    <div class="tech-ball__ray-box">
      <div
        v-for="(ray, idx) in frontRays"
        :key="1 + idx"
        class="tech-ball__ray-positioner"
        :style="ray.style"
      >
        <div
          class="tech-ball__ray"
          :style="ray.rayStyle"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { techList } from '@/constants'
import { mapState, mapActions } from 'vuex'

export default {
  data: () => {
    const rayCount = 16
    let frontRays = []
    let backRays = []

    for (let i = 0; i < rayCount; i++) {
      const angle = (i + 0.5) / rayCount * Math.PI * 2
      const x = Math.sin(angle)
      const y = Math.cos(angle)
      const duration = 1 + Math.random()
      const delay = (1 + Math.random()) * duration

      const style = {
        height: `${(y * 0.4 + 0.8) * 100}%`,
        width: `${(y * 0.1 + 1) * 5}%`,
        left: `${50 + x * 50}%`,
        opacity: `${Math.abs(y * 0.8) + 0.2}`
      }
      const rayStyle = {
        'animation-delay': `${delay}s`,
        '-webkit-animation-delay': `${delay}s`,
        'animation-duration': `${delay * 0.4 * 2}s`,
        '-webkit-animation-duration': `${delay * 0.4 * 2}s`
      }

      const obj = { x, y, style, rayStyle }

      if (y >= 0) {
        frontRays.push(obj)
      } else {
        backRays.push(obj)
      }
    }

    return {
      techList,
      frontRays,
      backRays
    }
  },
  computed: {
    ...mapState(['currentTech', 'prevTech'])
  },
  methods: {
    ...mapActions(['nextTech'])
  }
}
</script>

<style lang="scss">
.tech-ball {
  &__logo {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: contain;
    background-position: center;
    filter: brightness(125%);
  }

  &__logo-back-light {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: contain;
    background-position: center;
  }

  .fadeOut {
    animation: tech-ball-fade-out .5s linear;

    @keyframes tech-ball-fade-out {
      from {
        opacity: 1;
        transform: scale(1);
      }
      to {
        opacity: .5;
        transform: scale(0);
      }
    }
  }

  .fadeIn {
    animation: tech-ball-fade-in .5s linear;

    @keyframes tech-ball-fade-in {
      from {
        opacity: .5;
        transform: scale(0);
      }
      to {
        opacity: 1;
        transform: scale(1);
      }
    }
  }

  &__ray {
    position: absolute;
    opacity: 0;
    left: 0;
    right: 0;
    height: 50%;
    background-image: radial-gradient(#fff8, #fff2 30%, transparent 60%);
    transform: scale(1.5);

    animation-name: ball-ray;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    -webkit-animation-name: ball-ray;
    -webkit-animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
  }

  &__ray-positioner {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  &__ray-box {
    position: absolute;
    top: -40%;
    left: -10%;
    right: -10%;
    bottom: -40%;
  }
}

@keyframes ball-ray {
  0% { bottom: 0%; opacity: 0 }
  40% { opacity: 1 }
  80% { bottom: 50%; opacity: 0 }
}

@-webkit-keyframes ball-ray {
  0% { bottom: 0%; opacity: 0 }
  40% { opacity: 1 }
  80% { bottom: 50%; opacity: 0 }
}
</style>
