<template>
  <v-app
    class="smooth-fast"
    :style="{ 'background-color': prevTech ? techList[prevTech].dark : techList[currentTech].dark }"
  >
    <v-app-bar
      app
      class="smooth-fast"
    >
      <v-toolbar-title>
        <v-tooltip bottom>
          <template v-slot:activator="{ on }">
            <v-btn
              dark
              text
              x-large
              v-on="on"
            >{{ name }}</v-btn>
          </template>
          <div class="d-flex justify-center">
            <img src="/img/face.jpg" class="face mx-auto" width="96px" />
          </div>
          <i class="v-icon notranslate mdi mdi-check theme--dark success--text mr-2" />
          <span>Software Engineering</span><br/>
          <i class="v-icon notranslate mdi mdi-check theme--dark success--text mr-2" />
          <span>Web Development</span><br/>
          <i class="v-icon notranslate mdi mdi-check theme--dark success--text mr-2" />
          <span>Hybrid Mobile App Development</span><br/>
          <i class="v-icon notranslate mdi mdi-check theme--dark success--text mr-2" />
          <span>Team Management</span><br/>
          <i class="v-icon notranslate mdi mdi-check theme--dark success--text mr-2" />
          <span>Maths & Algorithms</span><br/>
          <i class="v-icon notranslate mdi mdi-check theme--dark success--text mr-2" />
          <span>UI/UX & Animation Design</span><br/>
        </v-tooltip>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn
        class="d-md-none"
        icon
      >
        <v-icon>mdi-menu</v-icon>
      </v-btn>
      <div class="d-none d-md-inline">
        <v-btn
          v-for="(item, idx) in sections"
          :key="idx"
          :href="item.ref"
          text
        >
          <span class="mr-2">{{item.name}}</span>
        </v-btn>
      </div>
    </v-app-bar>
    <v-main class="pa-0">
      <home @send-message="sendMessage" />
      <skills :headerStyle="headerGlow" />
      <experience :headerStyle="headerGlow" />
      <portfolio :headerStyle="headerGlow" />
    </v-main>

    <common-footer />
  </v-app>
</template>

<script>
import Home from './views/Home'
import Skills from './views/Skills'
import Experience from './views/Experience'
import Portfolio from './views/Portfolio'
import { mapState, mapActions } from 'vuex'

import { name, sections, techList } from '@/constants'

export default {
  name: 'App',
  data: () => ({
    name,
    sections,
    techList
  }),
  computed: {
    ...mapState(['currentTech', 'prevTech']),
    headerGlow () {
      const factor = this.prevTech ? 3 : 5

      return {
        'text-shadow': `0 0 ${factor * 1}px white, 0 0 ${factor * 2}px white,
          0 0 ${factor * 4}px ${this.prevTech ? this.techList[this.prevTech].lighter : this.techList[this.currentTech].lighter},
          0 0 ${factor * 8}px ${this.prevTech ? this.techList[this.prevTech].light : this.techList[this.currentTech].light},
          0 0 ${factor * 12}px ${this.prevTech ? this.techList[this.prevTech].color : this.techList[this.currentTech].color},
          0 0 ${factor * 16}px ${this.prevTech ? this.techList[this.prevTech].dark : this.techList[this.currentTech].dark}`
      }
    }
  },
  components: {
    Home,
    Skills,
    Experience,
    Portfolio
  },
  methods: {
    ...mapActions(['startShuffler']),
    sendMessage (message) {
    }
  },
  mounted () {
    this.startShuffler()
  }
}
</script>

<style lang="scss">
.page {
  position: relative;
  min-height: 100vh;

  &-title {
    font-size: 48px;
    text-align: center;
    font-family: 'Alfa Slab One', cursive;
  }
}

.v-footer {
  margin-top: 1px;
  background-color: #000c !important;
}

@media screen and (min-width: 960px) {
  .page {
    &-title {
      font-size: 64px;
    }
  }
}

@media screen and (orientation: portrait) and (min-width: 960px) {
  .page {
    min-height: 0;
  }
}
</style>
