import { techList } from '@/constants'

export default {
  nextTech: ({ state, dispatch }) => {
    const techArr = Object.keys(techList)
    const idx = techArr.indexOf(state.currentTech)
    dispatch('setCurrent', techArr[(idx + 1) % techArr.length])
  },
  prevTech: ({ state, dispatch }) => {
    const techArr = Object.keys(techList)
    const idx = techArr.indexOf(state.currentTech)
    dispatch('setCurrent', techArr[(idx - 1 + techList.length) % techArr.length])
  },
  setCurrent: ({ state, dispatch }, stack) => {
    if (state.prevTech) { return }
    dispatch('startShuffler')

    state.prevTech = state.currentTech
    state.currentTech = stack
    clearTimeout(state.toggleTechTimer)
    state.toggleTechTimer = setTimeout(() => { state.prevTech = '' }, 500)
  },
  startShuffler: ({ state, dispatch }) => {
    if (state.prevTech) { return }
    clearTimeout(state.nextTechTimer)
    state.nextTechTimer = setTimeout(() => { dispatch('nextTech') }, 7500)
  }
}
