import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import PubNubVue from 'pubnub-vue'
import './plugins/gallery-viewer'
import './components'
import '@/styles/index.scss'
import '@mdi/font/css/materialdesignicons.min.css'

const { VUE_APP_PUBNUB_PUB, VUE_APP_PUBNUB_SUB } = process.env

Vue.use(PubNubVue, {
  publishKey: VUE_APP_PUBNUB_PUB,
  subscribeKey: VUE_APP_PUBNUB_SUB,
  ssl: true
})

Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
