<template>
  <div
    id="home"
    class="home page d-flex flex-column"
    v-bind="$attrs"
  >
    <v-snackbar
      v-model="snackbar"
      :color="techList[currentTech].material"
      top
    >
      <span>Hi, this is still in development!</span><v-spacer />
      <v-btn
        text
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>
    <home-sunrays />
    <v-container class="home__content flex-grow-1 d-flex pa-0">
      <div class="home__section">
        <div class="d-md-none my-8">
          <h2
            class="smooth mb-2"
            :style="{ color: techList[currentTech].lighter }"
          >
            My name is {{name}}
          </h2>
          <h1>
            I Am A Creative<br/>
            Web Developer<br />
          </h1>
        </div>
        <div class="home__dialog pa-5 pt-8 text-center">
          <h2 class="mb-3">Contact Me</h2>
          <v-text-field
            v-model="message.name"
            color="white"
            label="Your Name"
          />
          <v-text-field
            v-model="message.subject"
            color="white"
            label="Subject"
          />
          <v-textarea
            v-model="message.content"
            color="white"
            label="Your message here..."
            outlined
          />
          <p class="mt-1"><label class="v-label theme--dark">Currently open to REMOTE only...</label></p>
          <v-btn
            :color="`${techList[currentTech].material} darken-3`"
            class="mb-5"
            :href="emailLink"
          >Send Message</v-btn>
          <v-row no-gutters>
            <v-col
              v-for="(contact, idx) in contactList"
              :key="idx"
              cols="2"
            >
              <v-tooltip
                :color="contact.color"
                bottom
              >
                <template v-slot:activator="{ on }">
                  <v-btn
                    :color="contact.color"
                    fab
                    small
                    v-on="on"
                    :href="contact.ref"
                    target="_blank"
                  >
                    <v-icon>{{contact.icon}}</v-icon>
                  </v-btn>
                </template>
                <span>{{contact.tooltip}}</span>
              </v-tooltip>
            </v-col>
          </v-row>
        </div>
      </div>
      <div class="home__section non-selectable d-none d-md-flex">
        <div class="d-none d-md-flex align-self-center flex-grow-1 flex-column justify-center">
          <h1
            class="smooth mb-4 mt-12"
            :style="{ color: techList[currentTech].lighter }"
          >
            My name is {{name}}
          </h1>
          <h1 class="large">
            I Am A Creative<br/>
            Web Developer<br />
          </h1>
        </div>

        <div class="home__wizard__wrapper">
          <home-wizard class="home__wizard" />
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { contactList, name, techList } from '@/constants'

export default {
  data: () => ({
    name,
    contactList,
    techList,
    snackbar: false,
    message: {
      name: '',
      subject: '',
      content: ''
    }
  }),
  computed: {
    ...mapState(['currentTech']),
    emailLink () {
      const mailLink = 'mailto:aito.hideki@gmail.com?' +
        'Subject=' + this.message.subject + '&body=' +
        this.message.content.replaceAll('\n', '%0D%0A') + '%0D%0A%0D%0A' + this.message.name

      return mailLink
    }
  }
}
</script>

<style lang="scss">
.home {
  padding: 56px 16px 0;

  &:before {
    z-index: 1;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: radial-gradient(ellipse at top, #fff4 0%, #0000 30%, #000a 60%, #000c 66%);
    background-size: 200% 100%;
    background-repeat: no-repeat;
    background-position: top;
  }

  &:after {
    z-index: 3;
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: radial-gradient(ellipse at top, transparent 60%, black 70%);
    background-size: 300% 100%;
    background-repeat: no-repeat;
    background-position: top;
    pointer-events: none;
  }

  &__content {
    flex-direction: column;
    z-index: 3;
  }

  &__section {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    flex-grow: 1;

    &:nth-of-type(1) {
      align-items: center;
      padding: 20px 0 40px;
    }
    &:nth-of-type(2) {
      flex-grow: 0;
      justify-content: flex-end;
    }
  }

  &__dialog {
    z-index: 2;
    max-width: 380px;
    width: 100%;
    height: 550px;
    border-radius: 10px;
    background-image: linear-gradient(105deg, #0008, #0009, #0006, #0009, #0008);
    background-position: center;
    box-shadow: 5px 5px 15px #0008;
    margin: 20px 20px 40px;
  }

  &__wizard {
    flex-grow: 1;
    width: 100%;

    &__wrapper {
      width: 100%;
      display: flex;
      align-items: center;
      flex-direction: row;
    }
  }
}

@media screen and (min-width: 600px) {
  .home {
    &__wizard {
      &__wrapper {
        width: 60%;
      }
    }
  }
}

@media screen and (min-width: 960px) {
  .home {
    padding: 64px 24px 0;

    &__section {
      flex-grow: 1;
    }

    &__wizard {
      &__wrapper {
        width: 80%;
      }
    }
  }
}

@media screen and (min-width: 1904px) {
  .home {
    &__wizard {
      &__wrapper {
        width: 60%;
      }
    }
  }
}

@media screen and (orientation: landscape) and (min-width: 960px) {
  .home {
    &__content {
      flex-direction: row;
    }
    &__section {
      width: 50%;

      &:nth-of-type(2) {
        justify-content: flex-end;
        align-items: flex-start;
      }
    }

    &__wizard {
      flex-grow: 0;
    }
  }
}
</style>
